import http from '@/api/http.js';
import QSPopup from '@/components/QS-popup/QS-popup.vue';
import segmentedControl from '@/components/segmented-control.vue';
import getSharePoster from '@/components/QS-SharePoster/QS-SharePoster.js'
export default {
	components: {segmentedControl,QSPopup},
	data() {
		return {
			detailimgtxt:'',
			items: ['图文详情', '商品参数', '客户评价'],
			current: 0,  //tab 切换对应
			goods:{},//商品信息
			chooseNorms:[],//规格列表
			goodsStatus:true,  // 请求到商品信息时显示状态
			autoplay:false,  //
			normIn2:0,  // 规格选择 对应
			normIn3:0,
			buyNum:1  ,// 购买商品数量
			goodsId:0,  //商品id
			type:1,   //1 加入购物车   2 立即购买
			costprice:0,
			price:0,  //定金
			tailprice:0,//尾款
			memberPrice:0,  //会员价
			seniorMemberPrice:0,  //农户价
			stock:0,  //库存
			skuId:0,  //库存 id
			comment:[],  //评论
			normType:2,
			member:'',	//是否会员商品
			normInGroup:0,
			normNumber:0,//秒杀规格id
			phone:'',
			presaleId:0,
			poster: {},		//canvas需要  起
			qrShow: false,
			canvasId: 'default_PosterCanvasId',
			codeImg:'',
			coverImg:'',
			goodsName:'',
			actPrice:0,
			goodsDesc:'',//canvas需要  结
			domain_name:'',//域名
			spread_user_id:0,//推广人id
			userId:0,//分享到小程序自己的id
		}
	},
	onLoad(option) {
		console.log(option)
		if(option.member){
			this.member = option.member
		}
		
		this.presaleId = option.presaleid
		if(option.spread_user_id){
			this.spread_user_id = option.spread_user_id
		}
		if(option.scene){
			let scene = decodeURIComponent(option.scene)//参数二维码传递过来的参数
			this.presaleId = scene.split("=")[1]
			console.log(scene)
		}
		if(option.q){
			let q = decodeURIComponent(option.q)//参数二维码传递过来的参数
			console.log(q)
			let arr = q.split('?');
			let arr2 = arr[1].split('&')
			let goodsid = arr2[0].split('=')[1];
			let user_id = arr2[1].split('=')[1];
			this.presaleId = goodsid
			this.spread_user_id = user_id
			// this.presaleId = q.split("?")[1].split("=")[1]
		}
		if(this.spread_user_id){
			uni.setStorageSync('spread_user_id',this.spread_user_id)
		}else{
			uni.setStorageSync('spread_user_id',0)
		}
		this.getInfo()
		this.getPhone()
		this.getdomainName()
		this.getUserId()
	},
	onShareAppMessage(option) {
		let that = this
		return {
		    title: that.goodsName,
		    path: '/pages/index/advanceBuy?presaleid='+this.presaleId+ '&spread_user_id=' + this.userId,
			imageUrl:that.coverImg
		   }
	},
	onReady: function(res) {

				this.videoContext = uni.createVideoContext('myVideo')

			},
	methods:{
		getUserId(){
			let data = {
				token:uni.getStorageSync('token')
			}
			http('/api/User/get_userinfo','post',data).then(res=>{
				console.log(res)
				if(res.data.status == '200'){
					this.userId = res.data.data.id
				}
			})
		},
		hidepop(){
			this.hides('QSPopuplogin')
			this.hides('QSPopup2')
		},
		toLogin(){
			if(this.spread_user_id){
				uni.navigateTo({
					url:'/pages/index/logon/logon?islogon=4&id='+this.presaleId+'&spread_user_id=' + this.spread_user_id
				})
			}else{
				uni.navigateTo({
					url:'/pages/index/logon/logon'
				})
			}
			this.hides('QSPopuplogin')
		},
		show(ref,type) {
			this.$refs[ref].show();
			if(type == 1){
				this.type = 1
				if(this.chooseNorms.length>0){
					let that = this
					let normArr = []
					if(that.chooseNorms.length == 1){
						that.normIn2 = that.chooseNorms[0].son_norms_arr[0].id
					}
					if(that.chooseNorms.length == 2){
						that.normIn2 = that.chooseNorms[0].son_norms_arr[0].id
						that.normIn3 = that.chooseNorms[1].son_norms_arr[0].id
					}
					if(that.chooseNorms.length == 1){
						that.normType = 2
						normArr.push(that.normIn2)
						that.buyNum = 1
					}
					if(that.chooseNorms.length == 2){
						that.normType = 3
						if(that.normIn2 == 0 ||that.normIn3 ==0){
							return
						}
						normArr.push(that.normIn2,that.normIn3)
						that.buyNum = 1
					}
					let data = {
						token:uni.getStorageSync('token'),
						type:that.normType,
						goods_id:that.goodsId,
						norms_id:normArr.join(',')
					}
					http('/api/Goods/getNormsInfo','post',data).then(res=>{
						if(res.data.status == '200'){
							that.price =parseFloat(res.data.data.price) 
							that.costprice = parseFloat(res.data.data.price)
							// that.prices =parseFloat(this.goods.price) 
							that.memberPrice = res.data.data.member_price
							that.stock = res.data.data.stock
							that.skuId = res.data.data.sku_id
						}else{
							uni.showToast({
								title:res.data.message,
								icon:'none'
							})
						}
						console.log(res)
					})
				}
			}else if(type == 2){
				this.type = 2
				
			}
			if(this.goods){ 
				// this.price =parseFloat(this.goods.price) 
				// this.tailprice =parseFloat(this.goods.price) 
				this.normNumber = this.goods.norms_arr[0].id
				this.price = parseFloat(this.goods.norms_arr[0].price[0])
				this.tailprice = parseFloat(this.goods.norms_arr[0].price[1])
				
				this.costprice =parseFloat(this.goods.price) 
				this.memberPrice = this.goods.member_price
				this.stock = this.goods.stock
				this.skuId = this.goods.sku_id
			}
			
		},
		
		showCreate(){
			this.show('QSPopupCreate')
			this.hides('QSPopupShare')
		},
		hideCar(){
			this.hides('QSPopup2')
			this.normIn2 = 0
			this.normIn3 = 0
			this.normInGroup = 0
		},
		hides(ref){
			this.$refs[ref].hide()
			this.normIn = 10
			this.price = 0
			this.costprice = 0
			this.tailprice=0
			this.memberPrice = 0
			this.stock = 0
			this.buyNum = 1
		},
		onClickItem(index) {
			  if (this.current !== index) {
				this.current = index;
			  }
		},
		getInfo(){
			
			let data = {
					token:uni.getStorageSync('token'),
					presale_id:this.presaleId,
				}
			http('/api/Presale/presaleInfo','post',data).then(res=>{
				if(res.data.status =='200'){
					this.goods= res.data.data.seckilInfo
					if(res.data.data.seckilInfo.choose_norms){
						this.chooseNorms = res.data.data.seckilInfo.choose_norms
					}
					this.comment = res.data.data.comment
					this.goodsId = res.data.data.seckilInfo.goods_id
					this.skuId = res.data.data.seckilInfo.sku_id
					console.log(res)
					this.goodsStatus = true
					if(this.goods.details){
						let reg = new RegExp('<img', 'gi');
						this.goods.details = this.goods.details.replace(reg, `<img style="width: 100%;vertical-align:top;"`);
					}
					
					this.prices = res.data.data.seckilInfo.price
					this.actPrice = res.data.data.seckilInfo.reveal_price
					this.goodsName = res.data.data.seckilInfo.goods_name
					this.coverImg = res.data.data.seckilInfo.cover
					this.codeImg = res.data.data.seckilInfo.qr_code
					this.goodsDesc = res.data.data.seckilInfo.desc
				}else if(res.data.status == '400'){
					this.goods = res.data
					this.goodsStatus = false
				}
				
				console.log(res)
			})
		},
		BoFang(){
			if(this.autoplay==false&&this.goods.video){
				this.autoplay=true;
				this.videoContext.play();
			}
		},
		ZhanTing(){
			if(this.autoplay==true){
				this.autoplay=false
			}
		},
		joinColl(){
			let data = {
				token:uni.getStorageSync('token'),
				goods_id:this.goodsId,
				type:3,
				activity_id:this.presaleId
			}
			http('/api/User/coll_goods','post',data).then(res=>{
				uni.showToast({
					title:res.data.msg,
					icon:'none'
				})
				console.log(res)
			})
			setTimeout(()=>{
				this.getInfo()
			},50)
		},
		normGroup(item,index){
			let that = this
			that.normInGroup = index
			that.normNumber = item.id
			that.price = item.price[0]
			that.tailprice = item.price[1]
			that.buyNum = 1
			console.log(that.normNumber)
			let data = {
				token:uni.getStorageSync('token'), 
				presale_id:that.presaleId,
				num:that.buyNum,
				norms_id:that.normNumber
			}
			http('/api/Presale/ajaxCheckPresale','post',data).then(res=>{
				if(res.data.status == '200'){
					
				}else{
					this.$util.showToast(res.data.msg)
					return
				}
				
				console.log(res)
			})
		},
		normTap(index2,index3,id){
			let that = this
			let normArr = []
			
			if(index2 == 0){
				that.normIn2 = id
			}
			if(index2 == 1){
				that.normIn3 = id
			}
			if(that.goods.choose_norms.length == 1){
				that.normType = 2
				normArr.push(id)
				that.buyNum = 1
			}
			if(that.goods.choose_norms.length == 2){
				that.normType = 3
				if(that.normIn2 == 0 ||that.normIn3 ==0){
					return
				}
				normArr.push(that.normIn2,that.normIn3)
				that.buyNum = 1
			}
		
			let data = {
				token:uni.getStorageSync('token'),
				type:that.normType,
				goods_id:that.goodsId,
				norms_id:normArr.join(',')
			}
			http('/api/Goods/getNormsInfo','post',data).then(res=>{
				if(res.data.status == '200'){
					that.costprice =parseFloat(res.data.data.price) 
					// that.prices =parseFloat(this.goods.price) 
					that.memberPrice = res.data.data.member_price
					that.stock = res.data.data.stock
					that.skuId = res.data.data.sku_id
				}else if(res.data.status == '400'){
					this.$util.showToast(res.data.msg)
				}
				console.log(res)
			})
		},
		isLogon(){
			if(uni.getStorageSync('token')){
				let data = {
					token:uni.getStorageSync('token')
				}
				http('/api/User/get_userinfo','get',data).then(res=>{
					if(res.data.status == '200'){
						this.toPay()
					}else{
						this.$util.showToast('登录信息已过期，请重新登录')
						let that = this
						setTimeout(()=>{
							if(that.spread_user_id){
								uni.navigateTo({
									url:'/pages/index/logon/logon?islogon=4&id='+that.presaleId+'&spread_user_id=' + that.spread_user_id
								})
							}else{
								uni.navigateTo({
									url:'/pages/index/logon/logon'
								})
							}
						},1500)
						return
					}
					
					console.log(res)
				})
			}else{
				this.show('QSPopuplogin')
			}
		},
		toPay(){
				
				if(this.type == 1){
					if(this.stock > 0 && this.stock >= this.buyNum){










						wx.setStorageSync('orderdata',{
							token:uni.getStorageSync('token'),
							type:1,
							goods_id:this.goodsId,
							attr_id:this.skuId,
							number:this.buyNum
						})

						uni.navigateTo({
							url:'/pages/index/pay/pay'
						})
						
					}else{
						uni.showToast({
							title:'未选择规格或库存不足',
							icon:'none'
						})
						return
					}
					
				}else if(this.type == 2){
					if(this.buyNum ){












						wx.setStorageSync('presaleOrderdata',{
							token:uni.getStorageSync('token'),
							type:1,
							goods_id:this.goodsId,
							attr_id:this.skuId,
							number:this.buyNum,
							presale_id:this.presaleId,
							norms_id:this.normNumber
						})

						let data = {
							token:uni.getStorageSync('token'), 
							presale_id:this.presaleId,
							num:this.buyNum,
							norms_id:this.normNumber
						}
						http('/api/Presale/ajaxCheckPresale','post',data).then(res=>{
							if(res.data.status == '200'){
								uni.navigateTo({
									url:'/pages/index/pay/advancePay'
								})
							}else{
								this.$util.showToast(res.data.msg)
								return
							}
							
							console.log(res)
						})
						
					}else{
						uni.showToast({
							title:'未选择规格或库存不足',
							icon:'none'
						})
						return
					}
				}
			
			
		},
		addNum(){
			// this.price = this.price*(this.buyNum+1);
			let costprices = 0
			let prices = 0
			let prices2 = 0
			if(this.buyNum){
				this.buyNum += 1
				costprices = (this.costprice * this.buyNum / (this.buyNum - 1)).toFixed(2)
				prices = (this.price * this.buyNum / (this.buyNum - 1)).toFixed(2)
				prices2 = (this.tailprice * this.buyNum / (this.buyNum - 1)).toFixed(2)
				console.log(prices)
				this.costprice = costprices
				this.price = prices
				this.tailprice = prices2
			}
			
			// if(this.buyNum >= 5){
			// 	return
			// }
			// if(this.buyNum < 5){      //定义一个 初始价格
			// 	this.buyNum += 1
			// 	this.price = this.prices * this.buyNum
			// }
		},
		jianNum(){
			let costprices = 0
			let prices = 0
			let prices2 = 0
			if(this.buyNum > 1){
				this.buyNum -= 1
				costprices = (this.costprice * this.buyNum / (this.buyNum + 1)).toFixed(2)
				prices = (this.price * this.buyNum / (this.buyNum + 1)).toFixed(2)
				prices2 = (this.tailprice * this.buyNum / (this.buyNum + 1)).toFixed(2)
				console.log(prices)
				this.costprice = costprices
				this.price = prices
				this.tailprice = prices2
			}
			if(this.buyNum <= 1){
				return
			}
		},
		getPhone(){
			http('/api/Setting/servicePhone','post').then(res=>{
				if(res.data.status == '200'){
					this.phone = res.data.data.service_phone
				}
				console.log(res)
			})
		},
		callPhone(){
			// let that = this
			// uni.makePhoneCall({
			 	
			//  	// 手机号
			//     phoneNumber:that.phone, 
			
			// 	// 成功回调
			// 	success: (res) => {
			// 		console.log('调用成功!')	
			// 	},
			
			// 	// 失败回调
			// 	fail: (res) => {
			// 		console.log('调用失败!')
			// 	}
				
			//   })
			//   that.hides('QSPopupkefu')
			uni.navigateTo({
				url: '/pages/index/totalList?kefu=kefu'
			})   
		},
		getdomainName(){
			http('/Api/setting/downloadInfo','post').then(res=>{
				this.domain_name = res.data.data.domain_name
				
			})
		},
		shareWeixin(){
			uni.share({
			 //    provider: "weixin",
			 //    scene: "WXSceneSession",
			 //    type: 0,
			 //    href: this.domain_name+"/#/pages/index/advanceBuy?presaleid="+this.presaleId,
				// title: '我在北申良品发现一个宝贝',
			 //    summary: this.goodsName,
			 //    imageUrl: this.coverImg,
				provider: 'weixin',
				scene: "WXSceneSession",
				type: 5,
				imageUrl:this.coverImg,
				title:this.goodsName,
				miniProgram: {
					id: 'gh_921dd6574e02',
					path: 'pages/index/advanceBuy?presaleid='+this.presaleId+ '&spread_user_id=' + this.userId,
					type: 0,
					webUrl: this.domain_name+"/#/pages/index/advanceBuy?presaleid="+this.presaleId+ '&spread_user_id=' + this.userId
				},
			    success: function (res) {
			        console.log("success:" + JSON.stringify(res));
			    },
			    fail: function (err) {
			        console.log("fail:" + JSON.stringify(err));
			    }
			})
		},
		shareCircleWeixin(){
			uni.share({
			    provider: "weixin",
			    scene: "WXSenceTimeline",
			    type: 0,
			    href:this.domain_name+ "/#/pages/index/advanceBuy?presaleid="+this.presaleId+ '&spread_user_id=' + this.userId,
			    title: '我在北申良品发现一个宝贝',
			    summary: this.goodsName,
			    imageUrl: this.coverImg,
			    success: function (res) {
			        console.log("success:" + JSON.stringify(res));
			    },
			    fail: function (err) {
			        console.log("fail:" + JSON.stringify(err));
			    }
			})
		},
		async shareFc() {
			let that = this
			let goodsName1=''
			let goodsName2=''
			if(that.goodsName.length<=11){
				goodsName1 = that.goodsName
			}else if(that.goodsName.length>11&&that.goodsName.length<21){
				goodsName1 = that.goodsName.substring(0,11)
				goodsName2 = that.goodsName.substring(11,that.goodsName.length)
			}else{
				goodsName1 = that.goodsName.substring(0,11)
				goodsName2 = that.goodsName.substring(11,21) + '...'
